<template>
  <div class="home">
    <v-calendar :attributes='dot' />
    <div class="add-input">
      <div class="sub-num">
        <p class="sub-num_inner is-id01"><input type="text" id="id01" class="input is-fix" v-model="id01" size="13" placeholder="AXAWEB-"></p>
        <p class="sub-num_inner is-id02">AXABW-<input type="text" id="id02" class="input is-fix" v-model="id02" size="4"></p>
      </div>
      <input type="text" id="title" class="input" v-model="title" placeholder="課題名">
      <div class="">
        <div class="calender-wrap">
          <p class="calender-wrap_sub">コーディング期間</p>
          <div class="calender-wrap_cal"><v-date-picker mode='range' v-model='range' formats="input: ['YYYY/MM/DD'], data: ['YYYY/MM/DD']" /></div>
        </div>
        <div class="calender-wrap">
          <p class="calender-wrap_sub">投入</p>
          <div class="calender-wrap_cal"><v-date-picker mode='multiple' v-model='dates' formats="input: ['L']" /></div>
        </div>
        <div class="select-wtap">
          <div class="status"><v-select v-model="selected" :options="['未着手', '作業中', '待ち', '公開済']" placeholder="ステータス"></v-select></div>
          <div class="memo is-appry"><input type="text" id="memo" class="input" v-model="memo" placeholder="メモ"></div>
          <div class="btn-wrap is-appry"><button class="btn" v-on:click="apply()">+</button></div>
        </div>
      </div>
    </div>
    <ul class="task-list">
      <li v-for="(item, index) in taskList" v-bind:key="index" v-bind:rel="index">
        <div class="sub-num">
          <a class="sub-num_inner is-id01" :href="'https://axa-bw.backlog.com/view/' + item.id01" target="_blank">{{item.id01}}</a>
          <a class="sub-num_inner is-id02" :href="'https://beeworks.backlog.jp/view/AXABW-' + item.id02" target="_blank">AXABW-{{item.id02}}</a>
        </div>
        <div class="memo-wrap"><input type="text" class="input" v-model="item.title" placeholder="課題名"></div>
        <div class="date-wrap">
          <div class="date-wrap_inner">
            <input type="text" class="input is-fix" size="10" v-model="item.kk.start"> 〜 <input type="text" class="input is-fix" size="10" v-model="item.kk.end">
          </div>
          <div class="date-wrap_inner is-post">
            <input type="text" class="input is-fix" size="10" v-model="item.date[0].dates[0]">
            <input type="text" class="input is-fix" size="10" v-model="item.date[0].dates[1]" v-if="item.date[0].dates[1] !== undefined">
          </div>
        </div>
        <div class="select-wtap">
          <div class="status">
            <v-select v-model="item.trans" :options="['未着手', '作業中', '待ち', '公開済']" :value="selected" placeholder="ステータス"></v-select>
          </div>
          <div class="memo"><input type="text" class="input" v-model="item.memo" placeholder="メモ"></div>
          <div class="btn-wrap">
            <button class="btn" v-on:click="change(item, index)">+</button>
            <button class="btn is-del" v-on:click="del(item, $event, index)">x</button>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import firebase from 'firebase/app';
import 'firebase/firestore';
import moment from 'moment';
export default {
  data() {
    return {
      title: '',
      id01: '',
      id02: '',
      memo: '',
      range: {},
      dates: [],
      selected: '',
      taskList: [],
      dot: []
    }
  },
  async created() {
    let dbList = [];
    async function re () {
      const db = firebase.firestore();
      const rest = await db.collection('task').get()
      return await rest
    }
    const t = await re()

    // Firestoreから取得した一覧を整える
    await Array.prototype.map.call(Object(t.docs), async value => {
      const dataArr = value.data().date
      const st = value.data().kk.start.seconds * 1000
      const ed = value.data().kk.end.seconds * 1000;
      let yoteilist = []
      let yoteiinputList = []

      let testline = {
        highlight: 'purple',
        popover: {label: `${value.data().id01}, ${value.data().id02}`},
        dates: {
          start: moment(st).format('YYYY,MM,DD'),
          end: moment(ed).format('YYYY,MM,DD')
        }
      }

      if(dataArr.length == 1) {
        // 投入予定1つのとき
        let timestmp = dataArr[0].seconds * 1000
        let timeformat = `${moment(timestmp).format('YYYY,MM,DD')}`
        yoteilist.push(`new Date(${timeformat})`)
        yoteiinputList.push(timeformat)

        let dotcolor = 'red'
        let yotei = {
          dot: {color: dotcolor},
          popover: {label: `${value.data().id01}, ${value.data().id02}`},
          dates: `new Date(${timeformat})`
        }
        this.dot.push(yotei)
      } else if(dataArr.length == 2) {
        // 投入予定2つ
        dataArr.forEach((item, index) => {
          let timestmp = item.seconds * 1000
          let timeformat = `${moment(timestmp).format('YYYY,MM,DD')}`
          yoteilist.push(`new Date(${timeformat})`)
          yoteiinputList.push(timeformat)

          let dotcolor = index == 0 ? 'blue' : 'red'
          let yotei = {
            dot: {color: dotcolor},
            popover: {label: `${value.data().id01}, ${value.data().id02}`},
            dates: `new Date(${timeformat})`
          }
          this.dot.push(yotei)
        })
      }
      this.dot.push(testline)

      let task = {
        "title": value.data().title,
        "kk": {
          start: `${moment(st).format('YYYY,MM,DD')}`,
          end: `${moment(ed).format('YYYY,MM,DD')}`
        },
        "trans": value.data().trans,
        "date": [{
          dot: {color: 'red'},
          dates: await yoteiinputList}],
        "id01": value.data().id01,
        "id02": value.data().id02,
        "memo": value.data().memo
      }
      dbList.push(task)
    })

    this.taskList = await dbList
  },
  methods: {
    // 初回登録
    apply() {
      const db = firebase.firestore();
      if(this.dates.length > 2) {
        this.dates.splice(2)
      }
      let task = {
        "title": this.title,
        "kk": {
          start: this.range.start,
          end: this.range.end
        },
        "trans": this.selected,
        "date": this.dates,
        "id01": this.id01,
        "id02": this.id02,
        "memo": this.memo
      }

      db.collection('task').doc(`${this.id01}-${this.id02}`).set(task, {merge: true});

      this.range.start = null;
      this.range.end = null;
      this.selected = null;
      this.dates = null;
      this.id01 = null;
      this.id02 = null;
      this.memo = null;
      this.title = null;
    },
    // 登録内容変更
    change(i) {
      const db = firebase.firestore();
      var coll = `${i.id01}-${i.id02}`;
      var docRef = db.collection('task').doc(coll);
      let sf = i.kk.start
      let setstart = firebase.firestore.Timestamp.fromDate(new Date(sf))

      let ef = i.kk.end.replace(/,/g, '')
      let setend = moment(ef).toDate()

      let df = i.date[0].dates.map(i => moment(i.replace(/,/g, '')).toDate())
      let setdate = df

      let ca = i.trans
      let task = {
        "title": i.title,
        "kk": {
          "start": setstart,
          "end": setend
        },
        "trans": ca,
        "date": setdate,
        "memo": i.memo
      }
      docRef.set(task, {merge: true});
      this.$forceUpdate();
    },
    // 登録削除
    del(item, e, index) {
      const db = firebase.firestore();
      var coll = `${item.id01}-${item.id02}`;
      db.collection('task').doc(coll).delete();
      this.taskList.splice(index, 1);
    }
  }
}

</script>
